.card {
    background: #fff;
    border-radius: 2px;
    height: 220px;
    margin: 5px 5px 10px -25px;
    padding: 5px 5px 5px 5px; 
    position: relative;
    width: 300px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    margin: auto;
   }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:linear-gradient(to bottom, #66ff99 0%, #000099 100%);
  }
  .card > .img-container > img {
    width: 60%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
nav {
    width: 100%;
    font-size: 2rem;
    color: #fff;
    margin-bottom: 50px;
    background-color: rgb(56, 56, 56);
    height: auto;
    font-family: 'Bungee', cursive;
  }
  
  a {
    color: #fed420;
  }
  
  a:hover {
    color: #fed420;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    /* text-align: center; */
  }
  
  ul {
    /* padding-left: 0px; */
    display: flex;
    align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
      font-size: 2rem;
    }
  }
.wrapper {
    background: rgb(175, 175, 175);
    background-image: url('https://i.pinimg.com/474x/70/5f/c3/705fc3f1b091086bd28b832eee608d9e--marvel-comics-superheroes-marvel-villains.jpg');
    background-repeat: no-repeat;
    height: 100%;
    background-size: cover;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    padding-bottom: 50px;
    
  }
.title {
    width: 100%;
    text-align: right;
    font-size: 2rem;
    color: #444;
   
    margin-bottom: 50px;
    padding-left: 10%;
    padding-right: 3%;  
  };

  @media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
      font-size: 2rem;
    }
  }
